@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-primary-color: #4884f8;
  --main-primary-dark: #042c7a;
  --main-cta-color: #4884f8;
  --main-lightblue-color: #ebf8ff;
  --main-black: #232624;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  color: #232624;
  color: var(--main-black);
}

button {
  padding: 9px 25px;
  font-weight: 700;
  background-color: #4884f8;
  background-color: var(--main-cta-color);
  color: #ffffff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

button:hover {
  background-color: #b5ceff;
}

li,
a {
  font-size: 16px;
  font-weight: 400;
  color: rgb(133, 138, 143);
  text-decoration: none;
}

a:hover {
  text-decoration: none;
  color: #4884f8;
  color: var(--main-primary-color);
}

.navbar {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 10px 0px;
}
.bg-light {
  background-color: white !important;
}

.white {
  color: white;
}

.bg-blue {
  background-color: #4884f8;
  background-color: var(--main-primary-color);
}

.bg-darkblue {
  background-color: #042c7a;
  background-color: var(--main-primary-dark);
}

.blue {
  color: #4884f8;
  color: var(--main-primary-color);
}

.bg-lightblue {
  background-color: #ebf8ff;
  background-color: var(--main-lightblue-color);
}

.p1 {
  font-size: 40px;
}
h1 {
  font-size: 40px;
  margin-bottom: 0 !important;
  line-height: 1.5 !important;
}

.p2 {
  font-size: 32px;
}

.p3 {
  font-size: 24px;
}

.p4 {
  font-size: 18px;
}

.p5 {
  font-size: 16px;
}

@media (min-width: 768px) {
  h1 {
    font-size: 52px;
  }

  .p1 {
    font-size: 52px;
  }

  .p2 {
    font-size: 48px;
  }

  .p3 {
    font-size: 32px;
  }

  .p4 {
    font-size: 24px;
  }

  .p5 {
    font-size: 16px;
  }
}

.mw-90 {
  max-width: 90%;
}

.secondary-btn {
  border: #4884f8 1px solid !important;
  border: var(--main-primary-color) 1px solid !important;
  color: #4884f8 !important;
  color: var(--main-primary-color) !important;
  background-color: white;
}

.btn {
  border-radius: 4px;
}

.default-px {
  padding-left: 10%;
  padding-right: 10%;
}

/* Override Bootstrap Classes */
.carousel-indicators li {
  background-color: #4884f8;
  background-color: var(--main-primary-color);
}

.carousel-control-prev {
  color: #4884f8;
  color: var(--main-primary-color);
}

.nav-link {
  margin-top: 20px;
}

.title {
  font-weight: 800;
  font-size: 45px;
  line-height: 120%;
  color: #222222;
}

.subtitle {
  max-width: 500px;
  line-height: 25px;
  color: #000000;
}

.letters-button {
  padding: 15px 50px;
  font-size: 18px;
}

.illustration {
  position: relative;
  z-index: 2;
  height: 300px;
}

.media-container {
  z-index: 999;
}
.media-article {
  max-height: 15px;
}

.letter-counter {
  border-bottom: 4px solid rgb(247, 247, 250);
}

@media (min-width: 768px) {
  .container {
    display: flex;
    justify-content: flex-start;
    padding: 50px 300px;
  }

  .media-article {
    max-height: 20px;
  }

  .illustration {
    position: relative;
    z-index: 2;
    height: 100%;
  }

  .letters-button {
    max-width: 75%;
  }
}

.maincard-background {
  position: relative;
  z-index: 1;
  width: 100%;
  margin-top: -100px;
}

@media (min-width: 992px) {
  .maincard-background {
    position: relative;
    z-index: 1;
    width: 100%;
