  justify-content: flex-start;
  align-items: center;
  padding: 27px 10%;
}

.logo {
  cursor: pointer;
  width: 15%;
  height: 15%;
  margin-top: -20px;
}

.nav-links {
  list-style: none;
  margin-left: 67px;
}

.nav-links li {
  display: inline-block;
  padding: 0px 20px;
}

.nav-links li a {
  transition: all 0.3s ease 0s;
}

.nav-links li a:hover {
  color: #818991;
}

.login {
  transition: all 0.3s ease 0s;
}

.login:hover {
  color: #818991;
}

.auth {
  margin-left: auto;
  margin-top: 20px;
}

.profile-picture {
  height: 250px;
  width: 250px;
  object-fit: cover;
}

.footer-wrapper {
